@import './assets/css/var.scss';

@font-face {
  font-family: 'Lato';
  src: url(../src/assets/fonts/Lato-Regular-15.ttf);
}
@font-face {
  font-family: 'Lato';
  font-weight: 500;
  src: url(../src/assets/fonts/Lato-Medium-13.ttf);
}
@font-face {
  font-family: 'Lato';
  font-weight: bold;
  src: url(../src/assets/fonts/Lato-Bold-7.ttf);
}

html {
  background: #120222;
  font-size: 10pt;

  body {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    min-height: 100vh;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    font-size: 14px;
    font-family: 'Lato', 'PingFang SC', 'Hiragino Sans GB', 'Heiti SC', 'Microsoft YaHei', 'WenQuanYi Micro Hei',
      sans-serif;

    .ant-select-item-option {
      margin: 2px;
      border-radius: 4px;
    }
    .ant-select-item-option-active:not(.ant-select-item-option-disabled) {
      background: rgba(89, 21, 225, 0.15);
      // color: #5915E1;
    }

    .ant-select-dropdown,
    .ant-select-item,
    .ant-select-item-option-selected:not(.ant-select-item-option-disabled) {
      background: #fff;
      color: #111;
    }

    .ant-select-dropdown {
      &.gover-dropdown {
        .ant-select-item {
          height: 40px;
          line-height: 30px;
          padding: 5px 8px;
        }
      }
    }

    .modal-btn {
      display: inline-block;
      font-weight: 500;
      color: #4e545a;
      font-family: inherit;
      font-size: inherit;
      background: transparent;
      padding: 0.25em 1.5em;
      position: relative;
      -webkit-transition: all 0.2s ease-in;
      transition: all 0.2s ease-in;
      border: 1px solid #4e545a;
      cursor: pointer;
      border-radius: 4px;
      box-sizing: border-box;
      line-height: 28px;
    }

    a {
      color: #5916e1;

      &:hover {
        color: #5916e1;
      }
    }

    .ant-tabs-top > .ant-tabs-nav::before,
    .ant-tabs-bottom > .ant-tabs-nav::before,
    .ant-tabs-top > div > .ant-tabs-nav::before,
    .ant-tabs-bottom > div > .ant-tabs-nav::before {
      border: none;
    }

    .bold {
      font-weight: bold;
    }

    .ant-picker-focused {
      box-shadow: none;
      border-color: #e1e1e1;
    }

    .ant-picker:hover,
    .ant-picker-focused {
      box-shadow: none;
      border-color: #e1e1e1;
    }

    .ant-picker.active {
      border-color: #5916e1;
    }
  }

  .en-font {
  }

  .ant-input-affix-wrapper-disabled {
    background: #fff;
  }

  .ant-input-affix-wrapper-focused {
    border: 1px solid #5916e1;
    box-shadow: none;
  }

  .ant-input-affix-wrapper:not(.ant-input-affix-wrapper-disabled):hover {
    border-color: #5916e1;
  }
}

.ant-tooltip-inner {
  font-size: 12px;
}

.fieldset-title {
  font-weight: 700;
  line-height: 16px;
  font-size: 18px;
  position: absolute;
  top: 15px;
  left: 15px;
}

#root {
