  -ms-flex: 1 0 auto;
  flex: 1 0 auto;

  .ant-input:focus {
    box-shadow: none;
  }

  .ant-select.ant-select-focused {
    box-shadow: none;
    // border: none;
  }

  .ant-select.ant-select-focused:not(.ant-select-focused-disabled):hover {
    box-shadow: none;
    // border: none;
  }

  .ant-select.ant-select-focused:not(.ant-select-disabled).ant-select:not(.ant-select-customize-input)
    .ant-select-selector {
    box-shadow: none;
    // border: none;
  }

  .ant-select.ant-select-focused:not(.ant-select-disabled).ant-select:not(.ant-select-customize-input)
