    // border: none;
    box-shadow: none;
  }

  .cp {
    cursor: pointer;
  }

  .main {
    position: relative;
    // padding-bottom: 80px;
  }

  .container {
    // padding-bottom: 50px;
    min-height: calc(100vh - 40px);
  }

  h1 {
    color: #fff;
  }

  input::-webkit-outer-spin-button,
  input::-webkit-inner-spin-button {
    -webkit-appearance: none;
  }

  input[type='number'] {
    -moz-appearance: textfield;
  }

  .window {
    // background: #f1ebde;
    background: #fff;
    position: relative;
    cursor: default;
    margin: 2em auto;
    width: 90%;
    color: #333;
    padding: 30px 24px;
    border-radius: 15px;
    box-shadow: 0px 20px 40px rgba(148, 148, 148, 0.0606);
    // -webkit-box-shadow: 7px 7px 0px rgba($color: #000, $alpha: 0.3);
    // box-shadow: 7px 7px 0px rgba($color: #000, $alpha: 0.3);
    max-width: min(728px, 90vw);
    // border: 6px double #dacdaf;
    // border-radius: 15px;

    &.pl-0 {
      padding: 30px 0;

      .ant-table table tr {
        padding: 0 24px;
      }
    }

    &.charts {
      min-height: 200px;

      ul.li-inline {
        li {
          display: inline-block;
          list-style: none;
          margin-right: 10px;
          cursor: pointer;
          user-select: none;
        }
      }
    }
  }

  > div {
    padding-bottom: 230px;
  }

  .background {
    position: fixed;
    width: 100vw;
    height: 100vh;
    top: 0;
    background: #120222 url(../src/assets/images/bg.png) top no-repeat;
    background-size: 100% 100%;

    // .bg_planets {
    //   background-image: url(../src/assets/images/bg_planets.svg);
