  footer {
    width: 100%;
    position: absolute;
    bottom: 0;
    background: #1e1e2f;
  }

  .base-footer {
    margin: 0 auto;
    padding: 40px 0;
    width: 100%;
    max-width: min(1200px, 90vw);
    display: flex;
    .footer-l {
      display: flex;
      .footer-list {
        flex: 1;
        margin-right: 90px;
        text-align: left;
        &:last-child {
          margin-right: 0;
        }
        .footer-title {
          white-space: nowrap;
          margin-bottom: 20px;
          font-size: 15px;
          font-family: Helvetica-Bold, Helvetica;
          font-weight: 700;
          color: #fff;
          line-height: 18px;
        }
        ul {
          li {
            margin-bottom: 10px;
            &:last-child {
              margin-bottom: 0;
            }
          }
          a {
            font-size: 15px;
            font-family: Helvetica;
            font-weight: 300;
            line-height: 18px;
            white-space: nowrap;
            color: rgba(255, 255, 255, 0.5);
            transition: color 0.1s;
            &:hover {
              color: #fff;
            }
          }
        }
      }
    }
    .footer-r {
      flex: 1;
      .sun-logo {
        display: flex;
        justify-content: flex-end;
        img {
          display: block;
          width: 152px;
        }
      }
      .copyright {
        margin-top: 30px;
        font-size: 12px;
        font-family: Helvetica;
        color: rgba(255, 255, 255, 0.5);
        line-height: 14px;
        text-align: right;
      }
    }
  }

