.ant-radio .ant-radio-inner {
  &::after {
    top: 2px;
    left: 2px;
    width: 8px;
    height: 8px;
    margin-top: 0;
    margin-left: 0;
    border-top: 0;
    border-left: 0;
    border-radius: 8px;
  }
}

.ant-radio-checked .ant-radio-inner {
  border: 2px solid #5915e1;

  &::after {
    background-color: #5914e1;
    transform: scale(1);
  }
}

.ant-checkbox-checked .ant-checkbox-inner {
  background-color: #5914e1;
  border-color: #5914e1;
}

.ant-checkbox-checked.ant-checkbox-disabled .ant-checkbox-inner {
  background-color: #f5f5f5;
}

.ant-checkbox-checked::after,
.ant-checkbox-wrapper:hover .ant-checkbox-inner {
  border-color: #5914e1;
}

.ant-select-item-option-selected:not(.ant-select-item-option-disabled) {
  font-weight: normal !important;
}

.wallet-text {
  font-weight: 400;
  font-size: 12px;
  line-height: 16px;
  color: #666666;
  text-align: center;
  margin-top: 10px;
}

@media screen and (max-width: 800px) {
  #root {
    .ant-select {
      width: unset;
    }

    .swap input {
      width: 150px;
    }

    .swap .w-190 {
      width: 150px;
    }

    fieldset {
      padding: 0;
    }

    button.sell {
      width: 60%;
    }

    .exchange-rate,
    .swap-pool-des {
      justify-content: flex-start !important;
      align-items: flex-start !important;
    }

    .swap-pool-des {
      margin-top: 0px;
    }

    .window {
      width: calc(100% - 30px);
      padding: 30px 15px 32px;
    }

    .clickable {
      margin-bottom: 6px;
    }
  }
}

@media screen and (max-width: $mobile-width-breakpoint) {
  #root {
    & > div {
      padding-bottom: 330px;
    }
  }
  #root footer {
    padding: 0 15px;
    .base-footer {
      display: block;
      padding: 26px 0 20px;
      max-width: 100%;
      .footer-l {
        margin-bottom: 30px;
        .footer-list {
          flex: 1;
          margin: 0;
          ul {
            li {
              margin-bottom: 20px;
              &:last-child {
                margin-bottom: 0;
              }
            }
            a {
              font-size: 12px;
            }
          }
        }
      }
      .footer-r {
        .sun-logo {
          justify-content: flex-start;
          img {
            width: 109px;
          }
        }
        .copyright {
          margin-top: 14px;
          text-align: left;
        }
      }
    }
  }
}

